.download__container {
  display: flex;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  max-height: 350px;
  margin-top: 100px;
  border-radius: 10px;
  word-wrap: break-word;
  width: 100%;

}
.download__container--left {
  /* background-color: var(--green); */
  /* width: 60%; */
  height: 100%;
  align-items: center;
  display: flex;
}
.download__container--left-first {
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-color: var(--green);
  height: 350px;
  width: 350px;
}
.download__container-left-second img {
  position: relative;
  height: 100%;
}

.download__container--right {
  padding: 35px;
  word-wrap: break-word;
  width: 100%;
}
.download__container--right h1 {
  color: #615f5f;
  font-size: 40px;
  margin-bottom: 40px;
}

.download__container--right span {
  font-size: 25px;
  color: #615f5f;
  text-overflow: clip;
}

.download__container--right-store {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  align-items: center;
  transform: scale(0.5);
}

.download__container--right-store img{
  padding: 0px 20px;
}
