.home__container {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: var(--green);
  box-shadow:0 1px 20px 0 rgba(0, 0, 0, 0.2) inset;
  padding: 100px 200px;
  display: flex;
}

.home__container--info {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-right: 200px;
}

.home__container--info h1 {
  font-size: 80px;
  font-weight: 800;
  color: white;
}

.home__container--info span {
  font-size: 29px;
  margin: 30px 0px;
  color: white;
}

.home__container--info button {
  width: 300px;
  border-radius: 15px;
  font-size: 25px;
  border: 0px solid white;
  font-size: 28px;
}

.home__container--image{
  margin-left: 100px;
}
.home__container--info img {
  padding-left: 100px;
  transform: scale(2);
}