.what__container {
  display: flex;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  max-height: 350px; 
  margin: auto;
  
}
.what__container--left{
  /* background-color: var(--green); */
  /* width: 60%; */
  height: 100%;
  align-items: center;
  display: flex;
}
.what__container--left-first{
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-color: var(--green);
  height: 350px;
}
.what__container-left-second img{
  position: relative;
  height: 100%;
}

.what__container--right {
  padding: 35px;
}
.what__container--right h1 {
  color: #615f5f;
  font-size: 40px;
  margin-bottom: 40px;
}

.what__container--right span {
  font-size: 25px;
  color: #615f5f;
}
